import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './sn-help-panel.scss';
import {view} from './view';
import {actionNames} from './constants';
import {actionHandlers} from './actions/coreHandlers';
import {translatedTexts as translate} from '../commons/i18n';

/**
* SN-HELP-PANEL COMPONENT
* standalone component which will render help-panel on the page of any product/app
* and provide contextual help experience to the user, Help-panel content may include images,
* videos or related link, search option is also provided, if user wants
* to search any queries or document.
* @summery Provides contextual help-panel experience for any app/product to the user.
* @seismicElement sn-help-panel
**/

createCustomElement('sn-help-panel', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	initialState: {
		isRequestInProgress: true,
		documents: null,
		docId: null,
		showBack: true,
		emptyPanelMessage: translate.defEmptyContent,
		allowEdit: false,
		expandContent: {
			content: null,
			isExpanded: false,
			lastFocusableElement: null
		}
	},
	properties: {
		/**
        * Set opened property as true to open help-panel
        * @type Boolean
        **/
		opened: {
			default: false,
			type: Boolean,
			required: false
		},

		/**
        * Specify the context of particular page on which you want to show help-panel
        * @type String
        **/
		appContext: {
			required: true,
			type: String | Object
		},

		/**
		* Specify position where you want to show help-panel, it can be right, left, top, bottom.
		* @type String
		**/
		position: {
			default: 'right',
			type: String,
			required: false
		},

		/**
		* Specify heading for help-panel
		* @type String
		**/
		heading: {
			default: translate.heading,
			type: String,
			required: false
		},
		/**
		* Specify top value in pixels after which you want the panel to appear.
		**/
		top: {
			default: 0,
			type: Number,
			required: false
		}
	},
	actionHandlers,
	dispatches: {
		/**
        * Dispatched  when panel is shown
        * @actionType SN_HELP_PANEL#SHOWN
        **/
		[actionNames.PANEL_SHOWN]() {},

		/**
        * Dispatched  when panel is Hidden
        * @@actionType SN_HELP_PANEL#HIDDEN
        **/
		[actionNames.PANEL_HIDDEN]() {}
	}
});
