import {dataHandlerURLs, dataHandlerActions} from './constants';
import {contentType} from '../../../commons/constants';
import {actionTypes} from '@servicenow/ui-core';
import {createHttpEffect} from '@servicenow/ui-effect-http';

const fetchDocContent = (url, pathParams) => createHttpEffect(url, {
	method: 'GET',
	batch: false,
	pathParams,
	successActionType: dataHandlerActions.FETCH_RESOURCE_SUCCEEDED,
	errorActionType: dataHandlerActions.FETCH_RESOURCE_FAILED
});

export const actionHandlers = {
	[actionTypes.COMPONENT_CONNECTED]({properties, dispatch, updateState}) {

		if (properties.type !== contentType.VIDEO) {
			properties.sysId ? dispatch(dataHandlerActions.FETCH_RESOURCE_BY_SYS_ID, {sys_id: properties.sysId}) :
				dispatch(dataHandlerActions.FETCH_RESOURCE_BY_RES_ID, {resource_id: properties.resourceId});
			return;
		}
		updateState({isRequestFailed: false});
	},

	[dataHandlerActions.FETCH_RESOURCE_BY_SYS_ID]: fetchDocContent(dataHandlerURLs.FETCH_RESOURCE_BY_SYS_ID, ['sys_id']),

	[dataHandlerActions.FETCH_RESOURCE_BY_RES_ID]: fetchDocContent(dataHandlerURLs.FETCH_RESOURCE_BY_RES_ID, ['resource_id']),

	[dataHandlerActions.FETCH_RESOURCE_SUCCEEDED]: ({properties, action, updateState}) => updateState({
		config: {...action.payload.result,
			type: properties.type,
			url: properties.url
		},
		isRequestFailed: false
	}),

	[dataHandlerActions.FETCH_RESOURCE_FAILED]: ({updateState}) => updateState({isRequestFailed: true})

};
