import {createCustomElement} from '@servicenow/ui-core';
import snabbdom, {Fragment} from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-loader';
import '../../presentational/hp-empty';
import styles from './hp-doc.scss';
import {translatedTexts as t} from '../../../commons/i18n';
import {hpDocActionHandlers} from './actionHandlers';
import '../hp-doc-content';
import '../../presentational/hp-feedback';
import {HP_DOC_BACK_ACTION} from './constants';

const view = state => {
	if(state.isRequestInProgress)
		return <now-loader label={`${t.loading}`} size="lg" className={'loader'}/>;

	if(!state.htmlContent)
		return <hp-empty/>;

	return (
		<Fragment>
			<div className={'hp-doc-header'}>
				{
					state.properties.showBack ? (
						<now-button-iconic icon="chevron-left-fill" size="md" variant="tertiary" bare={true}
							configAria={{'aria-label': t.backButtonLabel}}
							className={'hp-doc-header-back-icon'} append-to-payload={{userAction: HP_DOC_BACK_ACTION}}/>
					) : null
				}
				<p className='hp-doc-header-title'>{t.backButtonLabel}</p>
			</div>
			{state.htmlContent}
			{state.showFeedback ? <hp-feedback/> : null}
		</Fragment>
	);
};

createCustomElement('hp-doc', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	actionHandlers: hpDocActionHandlers,
	initialState: {
		htmlContent: null,
		isRequestInProgress: true,
		showFeedback: true,
		topicId: ''
	},
	properties: {
		sysId: {
			required: true,
			schema: {
				type: 'string'
			}
		},
		showBack: {
			required: false,
			default: true,
			schema: {
				type: 'boolean'
			}
		}
	},
});
