import {internalActions, actionNames, dataHandlerActions} from '../constants';
import {actionTypes} from '@servicenow/ui-core';
import {observable} from '../observable';
import {
	fetchHelp, fetchHelpFailed, fetchHelpSucceeded
} from './dataHandlers';
import {HP_DOC_LIST_DOC_CLICKED} from '../../components/presentational/hp-doc-list';
import {HP_DOC_BACK_CLICKED, HP_EDIT_PANEL} from '../../components/connected/hp-doc/constants';
import {actionNames as hpDocContentActions} from '../../components/connected/hp-doc-content/constants';
import {getURL} from '../../commons/utils';
import {handleFocusFirstNode} from '@servicenow/behavior-focus';

const messageQueue = observable.getInstance();

export const actionHandlers = {
	[actionTypes.COMPONENT_CONNECTED]({dispatch, updateProperties, updateState}) {

		messageQueue.subscribe(actionNames.SHOW_PANEL, ({action}) => {
			if(!action.payload.appContext)
				return null;

			updateProperties({opened: true});
			updateState({
				isRequestInProgress: true,
				docId: null
			});

			dispatch(dataHandlerActions.HP_FETCH_HELP, {
				data: {
					route: action.payload.appContext,
					url: getURL()
				}
			});
			dispatch(actionNames.PANEL_SHOWN);
		});

		messageQueue.subscribe(actionNames.OPEN_DOCUMENT, ({action}) => {
			updateProperties({opened: true});
			updateState({
				docId: action.payload.docId,
				showBack: action.payload.showBack || true,
				isRequestInProgress: false
			});
			if(action.payload.appContext) {
				updateProperties({
					appContext: action.payload.appContext
				});
			}
			dispatch(actionNames.PANEL_SHOWN);
		});

		messageQueue.subscribe(actionNames.HIDE_PANEL, () => {
			updateProperties({opened: false});
			dispatch(actionNames.PANEL_HIDDEN);
		});
	},

	[actionTypes.COMPONENT_PROPERTY_CHANGED]({host, action, dispatch, updateState, properties}) {
		if(!properties.opened)
			return;

		//TODO: Use isEqual function to compare objects
		if(action.payload.name === 'appContext'
			&& JSON.stringify(action.payload.previousValue) !== JSON.stringify(action.payload.value)) {
			updateState({
				isRequestInProgress: true,
				docId: null
			});

			dispatch(dataHandlerActions.HP_FETCH_HELP, {
				data: {
					route: action.payload.value,
					url: getURL()
				}
			});
		}

		if(action.payload.name === 'opened')
			handleFocusFirstNode(host);
	},

	[internalActions.CLOSE_PANEL]({updateProperties, dispatch}) {
		updateProperties({opened: false});
		dispatch(actionNames.PANEL_HIDDEN);
	},

	[HP_DOC_LIST_DOC_CLICKED] ({updateState, action}) {
		updateState({
			docId: action.payload,
			allowEdit: false
		});
	},

	[HP_DOC_BACK_CLICKED] ({updateState, dispatch, properties, state}) {
		updateState({
			docId: null
		});

		if(!properties.appContext || state.documents)
			return;

		updateState({
			isRequestInProgress: true
		});
		dispatch(dataHandlerActions.HP_FETCH_HELP, {
			data: {
				route: properties.appContext,
				url: getURL()
			}
		});
	},

	[hpDocContentActions.EXPAND_HP_DOC_CONTENT]: ({updateState, action}) => updateState({expandContent: {
		isExpanded: true,
		content: action.payload.content,
		lastFocusableElement: action.payload.elm
	}}),

	['NOW_MODAL#OPENED_SET']: ({state, updateState}) => {
		state.expandContent.lastFocusableElement.focus();

		updateState({expandContent: {
			isExpanded: false,
			content: null,
			lastFocusableElement: null
		}});
	},

	[HP_EDIT_PANEL]: ({action, updateState}) => updateState({
		allowEdit: action.payload
	}),

	[dataHandlerActions.HP_FETCH_HELP]: fetchHelp,
	[dataHandlerActions.HP_FETCH_HELP_SUCCEEDED]: fetchHelpSucceeded,
	[dataHandlerActions.HP_FETCH_HELP_FAILED]: fetchHelpFailed
};
