import {createHttpEffect} from '@servicenow/ui-effect-http';
import {actionTypes} from '@servicenow/ui-core';
import {createElementFromNode} from '@servicenow/ui-renderer-snabbdom';
import {
	FETCH_DOCUMENT_URL, HP_DOC_BACK_ACTION, HP_DOC_BACK_CLICKED, POST_FEEDBACK_URL,
	HP_FETCH_DOCUMENT, HP_FETCH_DOCUMENT_FAILED, HP_FETCH_DOCUMENT_SUCCEEDED,
	HP_POST_FEEDBACK, HP_POST_FEEDBACK_FAILED, HP_POST_FEEDBACK_SUCCEEDED, versionMap, HP_EDIT_PANEL
} from './constants';
import {HP_FEEDBACK_THUMBS_DOWN_CLICKED, HP_FEEDBACK_THUMBS_UP_CLICKED} from '../../presentational/hp-feedback';

const fetchDocument = createHttpEffect(FETCH_DOCUMENT_URL, {
	method: 'GET',
	batch: false,
	pathParams: ['sys_id'],
	successActionType: HP_FETCH_DOCUMENT_SUCCEEDED,
	errorActionType: HP_FETCH_DOCUMENT_FAILED
});

const fetchDocumentSucceeded = ({action, updateState, dispatch}) => {
	const {doc} = action.payload.result;
	const element = document.createElement('div');
	element.classList.add('hp-doc-body');
	element.innerHTML = doc.eh_content ? doc.eh_content.content : null;

	updateState({
		isRequestInProgress: false,
		htmlContent: createElementFromNode(element),
		showFeedback: action.payload.result.showFeedback === 'true',
		topicId: `${doc.topic_id} ${versionMap[doc.version]}`
	});
	dispatch(HP_EDIT_PANEL, doc.allowEdit);
};

const fetchDocumentFailed = ({updateState, dispatch}) => {
	updateState({
		isRequestInProgress: false,
		htmlContent: null
	});
	dispatch(HP_EDIT_PANEL, false);
};

const postFeedback = createHttpEffect(POST_FEEDBACK_URL, {
	method: 'POST',
	batch: false,
	dataParam: 'data',

	successActionType: HP_POST_FEEDBACK_SUCCEEDED,
	errorActionType: HP_POST_FEEDBACK_FAILED
});

const postFeedbackAction = ({updateState}) => {
	updateState({
		showFeedback: false,
		shouldRender: false
	});
};

const getFeedbackPayload = (feedback, topicId = '') => ({
	feedback: feedback ? 'positive' : 'negative',
	position: 'top',
	source: 'helpPanel',
	topicId
});

export const hpDocActionHandlers = {
	[actionTypes.COMPONENT_CONNECTED]({properties, dispatch}) {
		dispatch(HP_FETCH_DOCUMENT, {sys_id: properties.sysId});
	},

	[actionTypes.COMPONENT_PROPERTY_CHANGED]({action, dispatch, updateState}) {
		if(action.payload.name === 'sysId' && action.payload.previousValue !== action.payload.value) {
			updateState({
				isRequestInProgress: true
			});
			dispatch(HP_FETCH_DOCUMENT, {sys_id: action.payload.value});
		}
	},

	['NOW_BUTTON_ICONIC#CLICKED']: function ({dispatch, action}) {
		if(action.payload.userAction === HP_DOC_BACK_ACTION)
			dispatch(HP_DOC_BACK_CLICKED);
	},

	[HP_FEEDBACK_THUMBS_UP_CLICKED]({dispatch, state}) {
		dispatch(HP_POST_FEEDBACK, { data: getFeedbackPayload(true, state.topicId) });
	},

	[HP_FEEDBACK_THUMBS_DOWN_CLICKED]({dispatch, state}) {
		dispatch(HP_POST_FEEDBACK, { data: getFeedbackPayload(false, state.topicId) });
	},

	[HP_FETCH_DOCUMENT]: fetchDocument,
	[HP_FETCH_DOCUMENT_SUCCEEDED]: fetchDocumentSucceeded,
	[HP_FETCH_DOCUMENT_FAILED]: fetchDocumentFailed,

	[HP_POST_FEEDBACK]: postFeedback,
	[HP_POST_FEEDBACK_SUCCEEDED]: postFeedbackAction,
	[HP_POST_FEEDBACK_FAILED]: postFeedbackAction
};
