import {createHttpEffect} from '@servicenow/ui-effect-http';
import {dataHandlerActions, dataHandlerURLs} from '../constants';

export const fetchHelp = createHttpEffect(dataHandlerURLs.HELP, {
	method: 'POST',
	batch: false,
	dataParam: 'data',

	successActionType: dataHandlerActions.HP_FETCH_HELP_SUCCEEDED,
	errorActionType: dataHandlerActions.HP_FETCH_HELP_FAILED
});

export const fetchHelpSucceeded = ({action, updateState}) => {
	const {result} = action.payload;
	updateState({
		isRequestInProgress: false,
		showBack: true,
		documents: result.documents,
		emptyPanelMessage: result.emptyPanelMessage,
		allowEdit: result.allowEdit
	});
};

export const fetchHelpFailed = ({updateState}) => {
	updateState({
		isRequestInProgress: false,
		allowEdit: false
	});
};


