import {internalActions, headerHeight, editContentLink} from './constants';
import '@servicenow/now-icon';
import {translatedTexts as t} from '../commons/i18n';
import '@servicenow/now-loader';
import '../components/presentational/hp-doc-list';
import '../components/connected/hp-doc';
import '../components/presentational/hp-empty';
import '@servicenow/now-modal';
import {renderContent} from '../commons/utils';
import Fragment from '@servicenow/ui-renderer-snabbdom/lib/fragment';

const getBody = state => {
	if(state.docId)
		return <hp-doc sys-id={state.docId} show-back={state.showBack}/>;

	if(state.documents && state.documents.length > 0)
		return <hp-doc-list documents={state.documents}/>;

	return <hp-empty content={state.emptyPanelMessage}/>;
};

const renderFooter = state => {
	if(!state.allowEdit)
		return ;

	const link = `${editContentLink}${state.docId ? state.docId : '-1'}`;

	return <div className='sn-help-panel-footer'>
		<a href={link} target="_blank">
			<now-icon size='md' icon="open-link-fill" bare hidePadding/>
			{state.docId ? t.editPanelContent : t.addHelpContent}
		</a>
	</div>;
};

export const view = (state, {dispatch}) => {
	const {opened, heading, top} = state.properties;
	const {content, isExpanded} = state.expandContent;
	return <div className={`sn-help-panel ${opened ? '-visible' : '-hidden'}`} style={{top:`${top}px`}}>

		{isExpanded ? <now-modal opened='true' size='md' headerLabel={content.title ? content.title : content.subtitle}>{renderContent(content, isExpanded)}</now-modal> : ''}

		<div className='sn-help-panel-header' style={{height:`${headerHeight}px`}}>
			<h2 className='sn-help-panel-heading'>{heading}</h2>
			<button className='sn-help-panel-close-button' on-click={() => dispatch(internalActions.CLOSE_PANEL)} aria-label={t.ariaCloseButton} title={t.ariaCloseButton}>
				<now-icon icon="close-fill" size="md"/>
			</button>
		</div>
		{state.isRequestInProgress ?
			<now-loader label={`${t.loading}`} size="lg" className={'loader'}/>
			: <Fragment>
				<div className={`sn-help-panel-body-container ${state.allowEdit? 'footer' : ''}`} style={{top:`${top+headerHeight}px`}}>
					<div className='sn-help-panel-body'>
						{getBody(state)}
					</div>
				</div>
				{renderFooter(state)}
			</Fragment>
		}

	</div>;
};
