import {actionNames} from './constants';
import {observable} from './observable';

const messageQueue = observable.getInstance();

export const snHelpPanelBehavior = {
	name: 'sn-help-panel-behavior',
	actionHandlers: {
		/**
		 * Handles action SN_HELP_PANEL#SHOW_REQUESTED emitted by app
		 */
		[actionNames.SHOW_PANEL]: helpers => messageQueue.trigger(actionNames.SHOW_PANEL, helpers),

		/**
		 * Handles action SN_HELP_PANEL#HIDE_REQUESTED emitted by app
		 */
		[actionNames.HIDE_PANEL]: helpers => messageQueue.trigger(actionNames.HIDE_PANEL, helpers),

		/**
		 * Handles action SN_HELP_PANEL#OPEN_DOCUMENT emitted by app
		 */
		[actionNames.OPEN_DOCUMENT]: helpers => messageQueue.trigger(actionNames.OPEN_DOCUMENT, helpers),
	}
};
