import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {translatedTexts as t} from '../../../commons/i18n';
import styles from './hp-empty.scss';
import '@servicenow/now-illustration';
import '@servicenow/now-icon';

const productDocLink = 'https://docs.servicenow.com/';

const view = state =>  <div className={'empty-content'}>
	<now-illustration illustration="no-data" size='md'/>
	<p className={'no-content-avail'}>{state.properties.content}</p>
	<p className={'browse-content'}>{t.browseContent}</p>
	<a href={productDocLink} target="_blank">
		<now-icon size='md' icon="open-link-fill" bare hidePadding/>
		{t.productDoc}
	</a>
</div>;

createCustomElement('hp-empty', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		content: {
			default: t.defEmptyContent,
			required: false,
			schema: {
				type: 'string'
			}
		}
	},
});
