import {createCustomElement} from '@servicenow/ui-core';
import snabbdom, {createRef} from '@servicenow/ui-renderer-snabbdom';
import {translatedTexts as t} from '../../../commons/i18n';
import styles from './hp-feedback.scss';

const HP_FEEDBACK_THUMBS_UP = 'HP_FEEDBACK_THUMBS_UP';
const HP_FEEDBACK_THUMBS_DOWN = 'HP_FEEDBACK_THUMBS_DOWN';

export const HP_FEEDBACK_THUMBS_UP_CLICKED = 'HP_FEEDBACK#THUMBS_UP_CLICKED';
export const HP_FEEDBACK_THUMBS_DOWN_CLICKED = 'HP_FEEDBACK#THUMBS_DOWN_CLICKED';

const actionHandlers = {
	['NOW_BUTTON_ICONIC#CLICKED']: ({action, dispatch}) => {
		if(action.payload.userAction === HP_FEEDBACK_THUMBS_UP)
			dispatch(HP_FEEDBACK_THUMBS_UP_CLICKED);
		else if(action.payload.userAction === HP_FEEDBACK_THUMBS_DOWN)
			dispatch(HP_FEEDBACK_THUMBS_DOWN_CLICKED);
	}
};

/*eslint no-param-reassign: "error"*/
const fadeOutFeedback = feedbackElem => {
	feedbackElem.style.opacity = 1;

	(function fadeOut() {
		if ((feedbackElem.style.opacity -= .03) < 0)
			feedbackElem.style.display = 'none';
		else
			requestAnimationFrame(fadeOut);
	})();
};

const view = () => {
	const targetRef = createRef();
	return (
		<div className={'hp-feedback'} ref={targetRef}>
			<p className={'hp-feedback-title'}>{t.feedbackText}</p>
			<div className={'hp-feedback-action'}>
				<now-button-iconic icon='thumbs-up-outline' size='lg' variant={'tertiary'} bare={true}
					configAria={{'aria-label': t.ariaPositiveFeedback}} tooltipContent = {t.ariaPositiveFeedback}
					append-to-payload={{userAction: HP_FEEDBACK_THUMBS_UP}} on-click={() => fadeOutFeedback(targetRef.current)}/>
				<now-button-iconic icon='thumbs-down-outline' size='lg' variant={'tertiary'} bare={true}
					configAria={{'aria-label': t.ariaNegativeFeedback}} tooltipContent = {t.ariaNegativeFeedback}
					append-to-payload={{userAction: HP_FEEDBACK_THUMBS_DOWN}} on-click={() => fadeOutFeedback(targetRef.current)}/>
			</div>
		</div>
	);
};

createCustomElement('hp-feedback', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	actionHandlers
});
