import Fragment from '@servicenow/ui-renderer-snabbdom/lib/fragment';
import {dangerouslyCreateElementFromString} from '@servicenow/ui-renderer-snabbdom';
import {contentType} from './constants';

export const getURL = () => location.href;

export const renderContent = (config, isExpanded) => {
	const {image, image_link, image_alt, custom_html, title, subtitle, type, url} = config;
	const imageUrl = image ? image : (image_link ? image_link : url);

	switch (type) {
		case contentType.IMAGE:
			return <img src={imageUrl} alt={image_alt}/>;

		case contentType.TABLE:
			return <Fragment>
				{isExpanded ? '' : <p className='hp-doc-table-title'>{title ? title : subtitle}</p>}
				<div className='hp-doc-table'>
					{dangerouslyCreateElementFromString(`<Fragment>${custom_html}</Fragment>`)}
				</div>
			</Fragment>;
	}
};
