import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './hp-doc-list.scss';
import '@servicenow/now-icon';
import '../hp-empty';

export const HP_DOC_LIST_DOC_CLICKED = 'HP_DOC_LIST#DOC_CLICKED';

const itemRole = 'button';
/**
 * Triggers a click event when the element has focus and the Return key or the Space key is pressed.
 * @param event
 */
const onKeyDownHandler = event => {
	if(event.target.role === itemRole && (event.which === 13 || event.which === 32)) 
		event.target.click();
	
};

const view = (state, {dispatch}) => {
	if(state.properties.documents.length === 0)
		return <hp-empty/>;

	return (
		<ol className={'hp-doc-list'} on-keydown={onKeyDownHandler}>
			{state.properties.documents.map(doc => (
				<li>
					<div role={itemRole} data-sys_id={doc.sys_id} tabIndex={0} className={'hp-doc-list-item'}
						on-click={event => dispatch(HP_DOC_LIST_DOC_CLICKED, event.currentTarget.dataset.sys_id)}>
						<now-icon icon="document-outline" size="md" className={'hp-doc-list-item-icon'}/>
						<div className={'hp-doc-list-item-content'}>
							<h3 className={'hp-doc-list-item-title'}>{doc.name}</h3>
							<p className={'hp-doc-list-item-desc'}>{doc.description}</p>
						</div>
					</div>
				</li>
			))}
		</ol>
	);
};

createCustomElement('hp-doc-list', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		documents: {
			required: true,
			schema: {
				type: 'array',
				documents: {
					type: 'object',
					properties: {
						name: {type: 'string'},
						description: {type: 'string'},
						sys_id: {type: 'string'}
					}
				}
			}
		}
	},
	dispatches: {}
});
