import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './hp-doc-content.scss';
import {view} from './view';
import {actionNames} from './constants';
import {actionHandlers} from './actionHandlers';

createCustomElement('hp-doc-content', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	initialState: {
		config: null,
		isRequestFailed: true,
	},
	properties: {
		type: {
			type: String,
			default: ''
		},

		srcType: {
			type: String,
			default: ''
		},

		resourceId: {
			type: String,
			default: ''
		},

		sysId: {
			type: String,
			default: ''
		},

		url: {
			type: String,
			default: ''
		}
	},
	actionHandlers,
	dispatches: {
		/**
        * Dispatched  when expand button is clicked
        * @actionType HP_DOC_CONTENT#EXPAND_CLICKED
        **/
		[actionNames.EXPAND_HP_DOC_CONTENT]() {}
	}
});
