import {actionNames} from './constants';
import {contentType} from '../../../commons/constants';
import {translatedTexts as translate} from '../../../commons/i18n';
import {renderContent} from '../../../commons/utils';
import '@servicenow/now-button';
import '@servicenow/now-loader';
import {createRef} from '@servicenow/ui-renderer-snabbdom';

export const view = (state, {dispatch}) => {
	if(state.isRequestFailed)
		return ;
	const targetRef = createRef();
	return <div className='hp-doc-content'>
		{state.config ?
			<div className='hp-doc-content-body'>
				<now-button-iconic
					ref={targetRef} variant='tertiary'
					configAria={{'aria-label': translate.ariaExpandDocContent}}
					className='hp-doc-content-expand-button' icon='expand-fill' size='sm'
					tooltipContent={translate.ariaExpandDocContent}
					on-click={() => dispatch(actionNames.EXPAND_HP_DOC_CONTENT, {
						content: state.config,
						elm: targetRef.current
					})}
				/>
				{renderContent(state.config)}
			</div> : ''}
		{state.properties.type === contentType.VIDEO ?
			<iframe src={state.properties.url} width='100%' frameborder="0" allow="autoplay; fullscreen" allowfullscreen/>
			: '' }

	</div>;

};
